<template>
  <div class="paste-create">
    <div v-if="thereAreLinks(links.total)">
      <section class="section">
        <div class="container">
          <div class="columns is-multiline">
            <div class="column is-12">
              <b-button @click.prevent="createPaste()" label="Create" type="is-primary"></b-button>
            </div>
            <div class="column is-8">
              <b-field label="Name">
                <b-input v-model="form.name"></b-input>
              </b-field>

              <p v-if="errors">
                {{ errors.name}}
              </p>

            </div>
            <div class="column is-4">
              <b-field label="Description">
                <b-input type="textarea" v-model="form.body"></b-input>
              </b-field>
              <p v-if="errors">
                {{ errors.body }}
              </p>
            </div>
            <div class="column is-8">

              <b-tabs>

                <b-tab-item label="Table">
                    <b-table
                        :data="links"
                        :columns="columns"
                        :checked-rows.sync="checkedRows"
                        checkable
                        paginated
                        pagination-position="top"
                        per-page="5"
                        :checkbox-position="checkboxPosition"
                        :debounce-search="1000">

                        <template
                        v-if="column.searchable && !column.numeric"
                        slot="searchable"
                        slot-scope="props">
                        <b-input
                            v-model="props.filters[props.columns]"
                            placeholder="Search..."
                            icon="magnify"
                            size="is-small" />
                        </template>

                        <template slot="bottom-left">
                            <b>Total checked</b>: {{ checkedRows.length }}
                        </template>
                    </b-table>
                </b-tab-item>
                <b-tab-item label="Checked">
                  {{ checkedRows}}
                </b-tab-item>
              </b-tabs>

              <b-field grouped group-multiline>
                  <button class="button field is-danger" @click="checkedRows = []"
                      :disabled="!checkedRows.length">
                      <b-icon icon="close"></b-icon>
                      <span>Clear checked</span>
                  </button>
              </b-field>


            </div>
          </div>
        </div>
      </section>
    </div>
    <div v-else>
      <b-notification
        type="is-info"
        message="There are nothing links yet, please create new link"
        hasIcon
        :closable="false"
        ></b-notification>
    </div>
  </div>
</template>

<script>
import Link from '../../api/Link'
import Paste from '../../api/Paste'
// import TableLink from '@/components/app/TableLink.vue'

export default {
  data () {
    return {
      links: [],
      form: {
        name: '',
        body: '',
        links: []
      },
      checkedRows: [],
      checkboxPosition: 'left',
      columns: [
          {
              field: 'identy',
              label: 'Identy',
              numeric: false,
              searchable: true
          },
          {
            field: 'url',
            label: 'Url'
          },
          {
            field: 'name',
            label: 'Name'
          }
      ],
      errors: [],
    }
  },
  mounted () {
    Link.getLinks().then((response) => {
      this.links = response.data
      console.log(response.data.data)
    }).catch((error) => {
      console.log(error)
    })
  },
  methods: {
    thereAreLinks: function (total) {
      return total !== 0
    },
    createPaste: function () {
      this.form.links = this.checkedRows
      Paste.createPaste(this.form).then((response) => {
        console.log(response)
        if(response.status === 200){
          this.$buefy.notification.open({
            message: 'Paste created!',
            type: 'is-success'
          })
        }
      }).catch((error) => {
        console.log(error.response)
        if(error.response.status === 422) {

          this.errors = error.response.data.errors
          this.$buefy.toast.open('Error 422')
        }

        if(error.response.status === 500) {
          this.$buefy.toast.open('error 500')
        }
      })
    }
  }
}
</script>
